import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CashPartiesComponent } from './cash-parties/cash-parties.component';
import { CreditPartiesComponent } from './credit-parties/credit-parties.component';

const routes: Routes = [
  {
    path: 'cash',
    component: CashPartiesComponent
  },
  {
    path: 'credit',
    component: CreditPartiesComponent
  },
  {
    path: '',
    redirectTo: 'cash',
    pathMatch: 'full'
  }
];
@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PartiesRoutingModule { }
